body {
    background-color: #eee;
}
.wrap {
    max-width: 600px;
    .page-title {
        $color: #333;
        color: $color;
        font-size: 4.5rem;
        border-top: 10px solid $color;
        border-bottom: 10px solid $color;
    }
    .card_ {
        width: 100%;
        $card-base-font: 1rem;
        $card-border-color: #333;
        & > li {
            padding: 1rem 2.5rem;
            border: 1px solid $card-border-color;
            margin-bottom: -1px;
        }
        h3.location {
            font-size: $card-base-font * 2.25;
        }
        .date {
            font-size: $card-base-font;
        }
        .time {
            font-size: $card-base-font * 4.5;
            line-height: 1em;
        }
        h1,
        h2,
        h3,
        h4,
        h5,
        h6 {
            font-weight: bold;
        }
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6 {
        line-height: 1.4em;
    }
}
